<template>
  <v-card>
    <v-card-text>
      <v-row dense>
        <v-col class="d-flex"
               cols="12"
               sm="3">
          <mrc_ess_switch :true-value="1"
                          :false-value="0"
                          v-model="params.is_used"
                          @change="paramsChange"
                          :readonly="disabled"
                          :true-text='$t("label_To_enable_the")'
                          :false-text='$t("label_disable")'></mrc_ess_switch>
        </v-col>
      </v-row>
      <v-row dense>

        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_multiText :label="$t('label_role_name')+'*'"
                             :isNational="judegNational('role_name')"
                             :readonly="disabled"
                             @change="paramsChange"
                             style="width:100%"
                             :error-messages="$errors('params','role_name',requiredRule.concat(maxLength(20)))"
                             v-model="params.role_name"></mrc_ess_multiText>
          <!-- <v-text-field :error-messages="$errors('params','role_name',requiredRule.concat(maxLength(20)))"
                          @input="$v.params.role_name.$touch()"
                          @blur="$v.params.role_name.$touch()"
                          :readonly="disabled"
                          :label="$t('label_role_name')+'*'"
                          @change="paramsChange"
                          append-icon="mdi-longitude"
                          v-model="params.role_name"></v-text-field> -->
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <!-- <v-text-field :error-messages="$errors('params','role_description',maxLength(70))"
                          @input="$v.params.role_description.$touch()"
                          @blur="$v.params.role_description.$touch()"
                          @change="paramsChange"
                          :readonly="disabled"
                          :label="$t('label_role_describe')"
                          append-icon="mdi-longitude"
                          v-model="params.role_description"></v-text-field> -->
          <mrc_ess_multiText :error-messages="$errors('params','role_description',maxLength(70))"
                             @change="paramsChange"
                             style="width:100%"
                             :readonly="disabled"
                             :label="$t('label_role_describe')"
                             v-model="params.role_description"></mrc_ess_multiText>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_datePicker :errMsg="$errors('params','effective_start_date',requiredRule.concat([{rule: 'beforeData',msg: '不能超过有效结束时间'}]))"
                              @blur="$v.params.effective_start_date.$touch()"
                              @getTime='paramsChange'
                              :disabled="disabled"
                              :label="$t('label_effective_start_date')+'*'"
                              v-model="params.effective_start_date"></mrc_ess_datePicker>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="6">
          <mrc_ess_datePicker :errMsg="$errors('params','effective_end_date',[{rule: 'afterData',msg: '不能低于有效开始时间'}])"
                              @blur="$v.params.effective_end_date.$touch()"
                              @getTime='paramsChange'
                              :disabled='disabled'
                              :label="$t('label_effective_end_date')"
                              v-model="params.effective_end_date"></mrc_ess_datePicker>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="12">
          <mrc_ess_prompt :errMsg="$errors('params','org_level_uuid',requiredRule)"
                          @getData='paramsChange'
                          :disabled='disabled'
                          :rules="false"
                          :label="$t('label_company')+'*'"
                          :chips='false'
                          multiple
                          :obj='unitObj'
                          v-model="org_level_uuidArr">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="6">
          <!-- <v-text-field :error-messages="$errors('params','sequence',requiredRule.concat(between('1-99')))"
                          @input="$v.params.sequence.$touch()"
                          @blur="$v.params.sequence.$touch()"
                          @change="paramsChange"
                          :readonly="disabled"
                          :label="$t('label_The_sorting')+'*'"
                          v-model="params.sequence"></v-text-field> -->
          <mrc_ess_NumInput :readonly="disabled"
                            :label="$t('label_The_sorting')+'*'"
                            v-model="params.sequence"></mrc_ess_NumInput>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       v-if="!disabled"
                       @success='success'
                       :loading='loading'
                       :cancelText="cancelText || $t('label_cancel')"
                       :successText="successText || $t('label_submit')"></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: true
    },
    loading: {
      default: false,
      type: Boolean
    },
    propsParams: {
      type: Object,
      default: () => { }
    },
    cancelText: {
      type: String
    },
    successText: {
      type: String
    }
  },
  data () {
    return {
      date: '',
      params: {
        role_name: '',
        role_description: '',
        is_used: 1,
        org_level_uuid: '',
        sequence: 1,
        effective_start_date: null,
        effective_end_date: null
      }
    };
  },
  validations: {
    params: {
      role_name: {
        required,
        maxLength: maxLength(20)
      },
      role_description: {
        maxLength: maxLength(70)
      },
      effective_start_date: {
        required,
        beforeData (value) {
          if (!this.params.effective_end_date) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.params.effective_end_date).valueOf()
          return prevData < nextData
        }
      },
      effective_end_date: {
        // required,
        afterData (value) {
          if (!this.params.effective_start_date) return true
          if (!this.params.effective_end_date) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.params.effective_start_date).valueOf()
          return prevData < nextData
        }
      },
      org_level_uuid: {
        required
      },
      sequence: {
        required,
        between: between(1, 99)
      }
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_role')
  },
  methods: {
    paramsChange () {
      this.$emit('change', this.params)
    },
    cancel () {
      this.$emit('cancel')
    },
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('success', this.params)
    }
  },
  computed: {
    // 单位多选
    org_level_uuidArr: {
      get () {
        let arr = []
        if (this.params.org_level_uuid) {
          arr = this.params.org_level_uuid.split(',')
        }
        return arr
      },
      set (val) {
        this.params.org_level_uuid = val.join()
      }
    },
    unitObj () {
      return { prompt_name: 'org_levels_manager_filter_company', user_uuid: this.$store.state.userData.uuid }
    }
  },
  watch: {
    propsParams: {
      handler (val) {
        this.params = val
      },
      deep: true
    }
  },
};
</script>

<style scoped>
</style>
